import React from 'react';
import './Card.css';
import Highcharts from 'highcharts'
import HighchartsReact from 'highcharts-react-official'

const newsLimit = 25;

class Card extends React.Component{
    constructor(){
        super();

        this.state = {

            currency: {},
            loading: false,
            error: null,
            highData: [],
            news: [],
            moreNews: [],
            lowData: [],
            limit: 100,
            // To whoever reads this, I apologize in advance..
            options: {
                title: {
                    text: ''
                },
                series:[{
                    name: "Price (USD)",
                    data: []
                }]
            }
        }
    }

    getHigh(){
        const currencyName = this.props.match.params.Name;

        fetch(`https://min-api.cryptocompare.com/data/histoday?fsym=${currencyName}&tsym=USD&limit=${this.state.limit}`)
        .then(response => {
            //Error handling
            return response.json().then(json => {
                return response.ok ? json : Promise.reject(json);
            });
        }).then((highPrice) => {
            let result = highPrice.Data.map(a => a.high);
            console.log(result);
            this.setState({options: {
                title: {
                    text: `Price of ${this.props.match.params.Name} over last ${this.state.limit} days.` 
                },
                series:[{
                    data: result
                }]
            }

            });
        }).catch((error) => {
            console.log(error);
        })
    }

    getLow(){
        const currencyName = this.props.match.params.Name;

        fetch(`https://min-api.cryptocompare.com/data/histoday?fsym=${currencyName}&tsym=USD&limit=${this.state.limit}`)
        .then(response => {
            //Error handling
            return response.json().then(json => {
                return response.ok ? json : Promise.reject(json);
            });
        }).then((lowPrice) => {
            let result = lowPrice.Data.map(a => a.low);
            this.setState({lowData: lowPrice});

        }).catch((error) => {
            console.log(error);
        })

    }
    
    getNews(){
        const currencyname = this.props.match.params.Name;

        fetch(`https://min-api.cryptocompare.com/data/v2/news/?categories=${currencyname}`).then(response =>{
            return response.json().then(json => {
                return response.ok ? json : Promise.reject(json);
            });
        }).then((newsFeed) => {
            console.log('heck', newsFeed);
            this.setState({news: newsFeed.Data.slice(0, newsLimit), loading: false});
    
        }).catch((error) => {
            this.setState({error: error, loading: false})
        });
    }

    getMoreNews(){
        const currencyname = this.props.match.params.Name;

        fetch(`https://min-api.cryptocompare.com/data/v2/news/?categories=Technology`).then(response =>{
            return response.json().then(json => {
                return response.ok ? json : Promise.reject(json);
            });
        }).then((newsFeed) => {
            console.log('heck', newsFeed);
            this.setState({moreNews: newsFeed.Data.slice(0, newsLimit), loading: false});
    
        }).catch((error) => {
            this.setState({error: error, loading: false})
        });
    }
  

    componentDidMount(){
        console.log(this.props)
        this.getHigh();
        this.getLow();
        this.getNews();
        this.getMoreNews()

    }

    render(){
        const renderedList = this.state.news.map((newsItem) => {
            return(
                <div className = "newsItem" onClick = {() => window.open(`${newsItem.url}`, "_blank")}>{newsItem.title}</div>
            );
        })
        const anotherRenderedList = this.state.moreNews.map((newsItem) => {
            return(
                <div className = "newsItem" onClick = {() => window.open(`${newsItem.url}`, "_blank")}>{newsItem.title}</div>
            );
        })
        return(
            <div className = "detail-container">
                <div id = "chart-container">
                    <HighchartsReact highcharts = {Highcharts}
                    options= {this.state.options} />
                </div>
                <div id = "news-container">
                    <p id = 'newstext'>Coin specific news:</p>
                    {renderedList}
                </div>
                <div className = "news-container-right">
                <p id = 'newstext'>Crypto News:</p>
                    {anotherRenderedList}
                </div>
            </div>

        );
    }
}

export default Card;